import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import {initServiceUrl} from "./Constants";


const ATTRIBUTES = {
  serviceUrl: 'service-url',
};

class WidgetElement extends HTMLElement {

  static get observedAttributes() {
    return Object.values(ATTRIBUTES);
  }

  connectedCallback() {
    const serviceUrl = this.getAttribute("service-url")
    initServiceUrl(serviceUrl);

    this.mountPoint = document.createElement('div');
    this.appendChild(this.mountPoint);

    ReactDOM.render(<App serviceUrl={serviceUrl}/>, this.mountPoint);
  }
}


customElements.define('details-widget', WidgetElement);

export default WidgetElement;

