import React, {useEffect, useState} from 'react';
import {K} from "../../Constants";
import {Button, Col, Row} from "react-bootstrap";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import Modal from 'react-bootstrap/Modal';

import {
  faCheckCircle,
  faCircleXmark,
  faPlaneCircleXmark,
  faXmarkCircle,
  faXmarksLines, faXmarkSquare
} from "@fortawesome/free-solid-svg-icons";
import {apiAccettaProposta, apiGetRichiesta, apiRifiutaProposta} from "../../http/api";
import {useQuery} from "../../http/helpers";


export const Esito = () => {
  const [richiesta, setRichiesta] = useState({});
  const [proposta, setProposta] = useState(false);
  const [showModal, setShowModal] = useState(false);
  console.log(showModal)


  const getRichiesta = (query) => {
    apiGetRichiesta(K.serviceUrl, query)
      .then(json => {
        setRichiesta(json)
      })
      .catch(e => console.log("err get richiesta"));
  };

  const accetta = (id) => {
    apiAccettaProposta(K.serviceUrl, id)
      .then(json => {
        console.log('ok')
      })
      .catch(e => console.log("err put"));
  };

  const rifiuta = (id) => {
    apiRifiutaProposta(K.serviceUrl, id)
      .then(json => {
        console.log('ok')
      })
      .catch(e => console.log("err put"));
  };

  let query = useQuery().get('id');

  useEffect(() => {
    getRichiesta(query);
  }, []);

  function currencyFormatDE(num) {
    return (
      num
        .toFixed(2) // always two decimal digits
        .replace('.', ',') // replace decimal point character with ,
        .replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.') + ' €'
    ) // use . as a separator
  }

  return (<>


      {!richiesta ?
        <span></span> :
        <>
          {proposta === false ?
            <div className={'align-items-center back-col p-4'}>
              <div className={"white-div align-content-center"}>
                {richiesta.esito === true ?
                  <>
                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <FontAwesomeIcon icon={faCheckCircle} size={"6x"}
                                         className={'green-check mb-4'}></FontAwesomeIcon>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <p className={"title align-content-center"}>La tua azienda è risultata finanziabile.</p>
                      </Col>
                    </Row>

                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <p className={'subtitle'}>Per completare la pratica leggi ed accetta la nostra proposta
                          commerciale </p>
                      </Col>
                    </Row>

                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <button className="custom-button-richiesta" onClick={() => setProposta(true)}> Vai alla proposta
                        </button>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <button className="custom-button-richiesta"> Ritorna alla home page</button>
                      </Col>
                    </Row>
                  </> :
                  <>
                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <FontAwesomeIcon icon={faCircleXmark} size={"6x"} color={'red'}
                                         className={'mb-4'}></FontAwesomeIcon>
                      </Col>
                    </Row>
                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <p className={"title align-to-center"}>Siamo spiacenti, al momento non
                          possiamo soddisfare la sua richiesta di finanziamento
                        </p>
                      </Col>
                    </Row>

                    <Row>
                      <Col md={12} className={'align-to-center'}>
                        <button className="custom-button-richiesta"> Ritorna alla home page</button>
                      </Col>
                    </Row>
                  </>
                }

              </div>

            </div>
            :
            <div className={'align-items-center back-col p-4'}>
              <div className={"white-div align-content-center"}>


                <Modal show={showModal} onHide={() => setShowModal(false)} >
                  <Modal.Header closeButton>
                    <Modal.Title >Non Accettare</Modal.Title>
                  </Modal.Header>

                  <Modal.Body>
                    <p>Sei sicuro di non volere accettare la nostra proposta?
                    </p>
                  </Modal.Body>

                  <Modal.Footer>
                    <Button variant="secondary" onClick={() => setShowModal(false)}>No</Button>
                    <Button variant="primary" onClick={() => {
                      rifiuta(richiesta.id);
                      setShowModal(false);
                    }}>Si</Button>
                  </Modal.Footer>

                </Modal>


                <Row>
                  <Col md={12} className={'align-to-center'}>
                    <p className={"title align-content-center"}>La Prefattibilità della tua richiesta ha dato esito
                      positivo</p>
                  </Col>
                </Row>

                <Row>
                  <Col md={12} className={'align-to-center'}>
                    <p className={'subtitle'}>Possiamo avviare il nostro processo di delibera per la seguente
                      operazione </p>
                  </Col>
                </Row>

                <Row>
                  <Col md={12} className={'align-to-center'}>
                    <span className={'subtitle-proposta'}>Importo: </span> <span
                    className={'subtitle'}>{richiesta.importo ? currencyFormatDE(richiesta.importo) : ''}</span>
                  </Col>
                </Row>
                <Row>
                  <Col md={12} className={'align-to-center'}>
                    <span className={'subtitle-proposta'}>Durata: </span> <span
                    className={'subtitle'}>{richiesta.durata}</span>
                  </Col>
                </Row>
                <Row>
                  <Col md={12} className={'align-to-center'}>
                    <span className={'subtitle-proposta'}>Tasso variabile: </span> <span className={'subtitle'}> euribor mese + spread {richiesta.tassoVariabile}%</span>
                  </Col>
                </Row>
                <Row>
                  <Col md={12} className={'align-to-center'}>
                    <span className={'subtitle-proposta'}>Spese Istruttoria: </span> <span className={'subtitle'}>2% del finanziamento</span>
                  </Col>
                </Row>

                <Row>
                  <Col md={12} className={'align-to-center'}>
                    <button className="custom-button-richiesta mx-2" onClick={() => setShowModal(true)}>Non accettare
                    </button>
                    <button className="custom-button-richiesta mx-2" onClick={() => accetta(richiesta.id)}>Accetta
                    </button>
                  </Col>
                </Row>


              </div>
            </div>
          }
        </>
      }


    </>
  );
}


